	// 	var gui = new dat.GUI();
	// 	gui.add(this.sceneMain, 'particleMass', 0.000001, 0.1);
	// 	gui.add(this.sceneMain, 'forceMass', 0.1, 1.0);
	// 	gui.add(this.sceneMain, 'minDist', 2, 10);
