import View from "../framework/View";
import Mesh from "../framework/Mesh";

export default class ViewSave extends View {

	constructor(transforms) {

		super(transforms, require("../../shaders/save.vert"), require("../../shaders/save.frag"));

		var positions = [];
		var coords = [];
		var indices = [];
		var colors = [];

		
		var detail = window.NS.GL.params.detail;
		var totDetail = detail * detail;
		
		var index = 0;

		
		var y = 1 - (2 / (detail * 2));
		
		var yStep = 2 / detail;
		var xStep = 2 / (detail * 2);

		var startX = -1;

		for (var row=0;row<detail;row++){
			
			// var x = -1 + (2 / (detail * 2));

			var x = startX + (2 / ((detail*2) * 2));
			for (var col=0;col<detail;col++){

				positions.push([x, y, 0]);
				indices.push(index);
				// colors.push( this.pointData[index] );
				colors.push([Math.random() * 1000 / 1000, Math.random() * 1000 / 1000, .5]);


				x += xStep;
				index++;

			}

			y -= yStep;
		}

		//vel
		startX = 0;
		y = 1 - (2 / (detail * 2));
		for (var row=0;row<detail;row++){
			
			var x = startX + (2 / ((detail*2) * 2));
			for (var col=0;col<detail;col++){

				positions.push([x, y, 0]);
				indices.push(index);
				colors.push([Math.random() * 1000 / 1000, Math.random() * 1000 / 1000, 0.5]);
				// colors.push([.5, .5, .5]);

				x += xStep;
				index++;

			}

			y -= yStep;
		}

		this.mesh = new Mesh(positions.length, indices.length, this.gl.POINTS);
		this.mesh.bufferVertex(positions);
		this.mesh.bufferIndices(indices);
		this.mesh.bufferData(colors, "aVertexColor", 3);
	}

	render() {

		this.shader.bind();

		this.draw(this.mesh);

	}
}



