import View from "../framework/View";
import Mesh from "../framework/Mesh";

export default class ViewCal extends View {

	constructor(transforms) {

		super(transforms, require("../../shaders/cal.vert"), require("../../shaders/cal.frag"));
		this.count = 0;
		var positions = [];
		var coords = [];
		// var indices = [0, 1, 2, 0, 2, 3];
		
		var indices = [0, 1, 2, 0, 2, 3];

		positions.push([-1,	-1,  0]);
		positions.push([ 1,	-1,  0]);
		positions.push([ 1,	 1,  0]);
		positions.push([-1,	 1,  0]);

		coords.push([0, 0]);
		coords.push([1, 0]);
		coords.push([1, 1]);
		coords.push([0, 1]);

		this.mesh = new Mesh(positions.length, indices.length, this.gl.TRIANGLES);
		this.mesh.bufferVertex(positions);
		this.mesh.bufferTexCoords(coords);
		this.mesh.bufferIndices(indices);
	}

	render(texture, mousePos, letterTexture, particleMass, forceMass, minDist, maxDist) {

		// this.transforms.push();

		this.shader.bind();

		this.shader.uniform("uTexture", "uniform1i", 0);
		texture.bind(this.shader, 0);

		this.shader.uniform("uLetterTexture", "uniform1i", 1);
		letterTexture.bind(this.shader, 1);

		this.shader.uniform("uAmountLetterAdj", "uniform1f", 0.6);

		this.shader.uniform("uMousePos", "uniform2fv", new Float32Array(mousePos));

		this.shader.uniform("uParticleMass", "uniform1f", particleMass);
		this.shader.uniform("uForceMass", "uniform1f", forceMass);
		this.shader.uniform("minDistance", "uniform1f", minDist);
		this.shader.uniform("maxDistance", "uniform1f", maxDist);

		this.draw(this.mesh);

		// this.transforms.pop();
	}
}



