import View from "../framework/View";
import Mesh from "../framework/Mesh";

export default class ViewForce extends View {

	constructor(transforms) {

		super(transforms, require("../../shaders/copy.vert"), require("../../shaders/force.frag"));

		this.mouseX = 0;
		this.mouseY = 0;

		// window.addEventListener('mousemove', (e) => {

		// 	this.onMouseMove(e);
		// });

		var positions = [];
		var coords = [];
		var indices = [0, 1, 2, 0, 2, 3];
		
		var size = 1;
		positions.push([-size, -size, 0]);
		positions.push([ size, -size, 0]);
		positions.push([ size,  size, 0]);
		positions.push([-size,  size, 0]);

		coords.push([0, 0]);
		coords.push([1, 0]);
		coords.push([1, 1]);
		coords.push([0, 1]);

		this.mesh = new Mesh(positions.length, indices.length, this.gl.TRIANGLES);
		this.mesh.bufferVertex(positions);
		this.mesh.bufferTexCoords(coords);
		this.mesh.bufferIndices(indices);
	}

	onMouseMove(e) {

		var relX = e.x / window.innerWidth;
		var relY = e.y / window.innerHeight;

		this.mouseX = relX;
		this.mouseY = relY;

		

	}

	render() {

		// this.transforms.push();

		this.shader.bind();


		this.shader.uniform("uMousePos", "uniform2fv", new Float32Array([this.mouseX, this.mouseY]));


		this.draw(this.mesh);

		// this.transforms.pop();
	}
}



