import Scene from "./framework/Scene";
import ViewSave from "./views/ViewSave";
import ViewCopy from "./views/ViewCopy";
import ViewCal from "./views/ViewCal";
import ViewMap from "./views/ViewMap";
import ViewForce from "./views/ViewForce";
import ViewLetterVel from "./views/ViewLetterVel";
import MouseInteractor from "./framework/MouseInteractor";
import KeyboardInteractor from "./framework/KeyboardInteractor";
import SceneTransforms from "./framework/SceneTransforms";
import Framebuffer from "./framework/Framebuffer";
import Texture from "./framework/Texture";
import SpectrumAnalyser from "./framework/SpectrumAnalyser";
import TextureCreator from "./tools/TextureCreator";

export default class SceneMain extends Scene {
	constructor() {

		super();
		this.onResize();
		window.addEventListener('resize', () => {
			
			this.onResize();
		});

		
		this.particleMass = .0004;
		this.forceMass = 0.5;
		this.minDist = 5;
		this.maxDist = 15;
		this.isKeyDown = false;

		this.mouseX = 0;
		this.mouseY = 0;

		this.hasSaved = false;

		this.camera.setPosition(new Array(0.0, 0.0, 5.0));
		this.camera.setLookAtPoint(vec3.fromValues(0.0, 0.0, 1.0));

		// this.mouseInteractor = new MouseInteractor(this.camera, this.canvas);
		// this.mouseInteractor.setup();

		// this.keyboardInteractor = new KeyboardInteractor(this.camera, this.canvas);
		// this.keyboardInteractor.setup();

		this.transforms = new SceneTransforms(this.canvas);

		this.orthoTransforms = new SceneTransforms(this.canvas);

		this.textureCreator = new TextureCreator();

		this.initViews();
		this.initTextures();

		this.mouseIsDown = false;
		this.patternStartCoord = [];


		//EVENTS
		window.addEventListener('mousemove', (e) => {

			this.onMouseMove(e);
		});

		window.addEventListener('mousedown', (e) => {

			this.onMouseDown(e);
		});

		window.addEventListener('mouseup', (e) => {

			this.onMouseUp(e);
		});

		// window.addEventListener('keydown', (e) => {

		// 	this.onKeyDown(e);
		// });

		window.addEventListener('keyup', (e) => {

			this.onKeyUp(e);
		});

	}

	onMouseDown(e) {

		this.mouseIsDown = true;
		this.patternStartCoord[0] = e.x;
		this.patternStartCoord[1] = e.y;

		this.textureCreator.startPattern(e);
	}

	onMouseUp(e) {

		this.mouseIsDown = false;
		this.textureCreator.endPattern(e);


		this.letterTexture = new Texture(this.textureCreator.canvas);

	}

	onMouseMove(e) {

		var relX = e.x / window.innerWidth;
		var relY = e.y / window.innerHeight;

		this.mouseX = relX;
		this.mouseY = relY;

		if (!this.mouseIsDown) return;

		this.textureCreator.draw(e);


	}

	onKeyDown(e) {

		
		// this.isKeyDown = true;

	}

	onKeyUp(e) {

		var letter = String.fromCharCode(e.keyCode);
		if (letter == " "){
			this.textureCreator.reset();
			this.letterTexture = new Texture(this.textureCreator.canvas);
		}
			

			// this.textureCreator.removeLetter();
		// else if (letter)
		// 	this.textureCreator.addLetter(letter);

		// // debugger;
		// var img = this.textureCreator.getImage();

		// this.letterTexture = new Texture(this.textureCreator.canvas);
		// this.letterTexture.updateTexture(this.textureCreator.canvas);


		// this.isKeyDown = false;
	}

	initTextures() {
	
		var size = window.NS.GL.params.detail;

		this.fboCurrent = new Framebuffer(size * 2, size, this.gl.NEAREST, this.gl.NEAREST, this.gl.FLOAT);
		this.fboCurrent.id = 'fboCurrent';

		this.fboTarget = new Framebuffer(size * 2, size, this.gl.NEAREST, this.gl.NEAREST, this.gl.FLOAT);
		this.fboTarget.id = 'fboTarget';

		this.fboLetterVel = new Framebuffer(size, size, this.gl.NEAREST, this.gl.NEAREST, this.gl.FLOAT);

		// this.textureCreator.getImage();
		this.letterTexture = new Texture(this.textureCreator.canvas);

		// this.letterTexture = new Texture(window.NS.Textures['testTexture_filled'], false);

		// this.letterTextureFilled = new Texture(window.NS.Textures['testTexture_filled'], false);

		// this.fboForce = new Framebuffer(256, 256, this.gl.NEAREST, this.gl.NEAREST, this.gl.FLOAT);

	}

	initViews() {

		this._vSave = new ViewSave(this.orthoTransforms);

		this._vCopy = new ViewCopy(this.orthoTransforms);

		this._vCal = new ViewCal(this.orthoTransforms);

		this._vMap = new ViewMap(this.transforms);

		this._vLetterVel = new ViewLetterVel(this.orthoTransforms);

		// this._vForce = new ViewForce(this.orthoTransforms);
		
	}

	update() {

		super.update();

	}

	render() {

		
		this.gl.disable(this.gl.DEPTH_TEST);

		this.gl.enable(this.gl.BLEND);

		
		if (!this.hasSaved){

		
			this.orthoTransforms.setCamera(this.orthoCamera);

			this.fboCurrent.bind();
			this.gl.viewport(0, 0, this.fboCurrent.width, this.fboCurrent.height);
			this.gl.clearColor( 0.5, 0.5, 0.5, 1 );
			this.gl.clear(this.gl.COLOR_BUFFER_BIT | this.gl.DEPTH_BUFFER_BIT);
			
			this._vSave.render();
			this.fboCurrent.unbind();

			

			this.fboTarget.bind();
			this.gl.clear(this.gl.COLOR_BUFFER_BIT | this.gl.DEPTH_BUFFER_BIT);
			this.fboTarget.unbind();

			
			// this.fboForce.bind();
			// this.gl.viewport(0, 0, this.fboForce.width, this.fboForce.height);
			// this.gl.clearColor( 0.0, 0.0, 0.0, 1 );
			// this.gl.clear(this.gl.COLOR_BUFFER_BIT | this.gl.DEPTH_BUFFER_BIT);

			// this._vForce.render();
			// this.fboForce.unbind();

		

			// this.gl.viewport(0, 0, this.gl.viewportWidth, this.gl.viewportHeight);

			// this._vCopy.render(this.fboCurrent.getTexture());

			// return;

			this.hasSaved = true;
			
			return;
		
		}

		this.orthoTransforms.setCamera(this.orthoCamera);

		// this.gl.viewport(0, 0, this.fboForce.width, this.fboForce.height);
		// this.fboForce.bind();
		// this._vForce.render();

		// this.fboForce.unbind();

		// this.gl.viewport(0,0,this.fboLetterVel.width, this.fboLetterVel.height);
		// this.fboLetterVel.bind();
		// this._vLetterVel.render(this.letterTexture);
		// this.fboLetterVel.unbind();

		// this.gl.viewport(0, 0, 512, 32);

		
		// this._vCopy.render(this.letterTexture);

		// this.swapFbos();

		// return;

		this.gl.viewport(0, 0, this.fboCurrent.width, this.fboCurrent.height);

		this.fboTarget.bind();

		this._vCal.render(this.fboCurrent.getTexture(), [this.mouseX, this.mouseY], this.letterTexture, this.particleMass, this.forceMass, this.minDist, this.maxDist);

		this.fboTarget.unbind();

		


		this.gl.viewport(0, 0, this.gl.viewportWidth, this.gl.viewportHeight);

		this.gl.clearColor( 0, 0, 0, 1 );
		this.gl.clear(this.gl.COLOR_BUFFER_BIT | this.gl.DEPTH_BUFFER_BIT);

		this.gl.enable(this.gl.DEPTH_TEST);

		this.gl.disable(this.gl.BLEND);

		this.transforms.push();
		this.transforms.setCamera(this.camera);

		this.camera.apply(this.gl.viewportWidth / this.gl.viewportHeight);
		this.transforms.calculateModelView();

		// this._postPreFBO.bind();
		// this.gl.clear(this.gl.COLOR_BUFFER_BIT | this.gl.DEPTH_BUFFER_BIT);
		this._vMap.render(this.fboTarget.getTexture());

		// this._postPreFBO.unbind();

		this.transforms.pop();


		//POST-Processing
		// this._blurVFBO.bind();
		// this.gl.clear(this.gl.COLOR_BUFFER_BIT | this.gl.DEPTH_BUFFER_BIT);

		// this._vBlurVert.render(this._postPreFBO.getTexture());

		// this._blurVFBO.unbind();

		// // this._blurHFBO.bind();
		// // gl.clear(gl.COLOR_BUFFER_BIT | gl.DEPTH_BUFFER_BIT);

		// this._vBlurHoriz.render(this._blurVFBO.getTexture());

		// this._blurHFBO.unbind();

		


	
		

		this.swapFbos();

	}

	swapFbos() {
		
		var tmp = this.fboTarget;
		this.fboTarget = this.fboCurrent;
		this.fboCurrent = tmp;
	};


}


