import View from "../framework/View";
import Mesh from "../framework/Mesh";

export default class ViewMap extends View {

	constructor(transforms) {

		super(transforms, require("../../shaders/map.vert"), require("../../shaders/map.frag"));

		var positions = [];
		var coords = [];
		var indices = [];
		var colors = [];
		
		var detail = window.NS.GL.params.detail;
		
		var index = 0;

		var nrPoints = detail * detail;
		
		for (var i=0;i<nrPoints;i++){

			positions.push([0, 0, 0]);
			
			var tx = i % detail;
			var ty = Math.floor(i / detail);
			var ux = tx / detail;
			var uy = ty / detail;

			coords.push([ux, uy]);
			
			indices.push(i);

			// console.log('ux: ',ux, ' uy: ',uy);
			
		}



		this.mesh = new Mesh(positions.length, indices.length, this.gl.POINTS);
		this.mesh.bufferVertex(positions);
		this.mesh.bufferTexCoords(coords);
		this.mesh.bufferIndices(indices);
		// this.mesh.bufferData(colors, "aVertexColor", 3);
	}

	getSphereUV() {

		var latitudeBands = window.NS.GL.params.detail-1;
        var longitudeBands = window.NS.GL.params.detail-1;
        
      	var uvs = [];
        for (var latNumber=0; latNumber <= latitudeBands; latNumber++) {
            

            for (var longNumber=0; longNumber <= longitudeBands; longNumber++) {
              
                var u = 1 - (longNumber / longitudeBands);
                var v = 1 - (latNumber / latitudeBands);

                uvs.push([u,v])

                
             	
            }
        }
       
        return uvs;
	}

	render(texturePos) {

		this.transforms.push();

		this.shader.bind();

		this.shader.uniform("uTexturePos", "uniform1i", 0);
		
		texturePos.bind(this.shader, 0);
		
		
		this.draw(this.mesh);

		this.transforms.pop();
	}
}



